
import { Base64 } from 'js-base64';

class Fetcher {
    constructor(engine, key, channel, baseUrl, info) {

        this.engine = engine;
        this.key = key;
        this.baseUrl = baseUrl;
        this.channelId = Base64.encode(channel);
        this.announceInfo = {...info, channel: this.channelId};
        //-----------bt---------------------
        this.announceURL = `${baseUrl}/channel`;
        this.key = key;

        //
        this.limit = 10*1024;                              //KB

        //
        this.conns = 0;                                                 //peer
        this.failConns = 0;                                             //peer

        //(KB)
        this.totalHTTPDownloaded = 0;         // HTTP
        this.totalP2PDownloaded = 0;          // P2P
        this.totalP2PUploaded = 0;            // P2P
        this.httpDownloaded = 0;
        this.p2pDownloaded = 0;

        //
        this.errsFragLoad = 0;                                      //
        this.errsBufStalled = 0;                                    //
        this.errsInternalExpt = 0;                                 //


    }

    btAnnounce() {
        const { logger } = this.engine;
        return new Promise((resolve, reject) => {
            fetch(this.announceURL, {
                headers: this._requestHeader,
                method: 'POST',
                body: JSON.stringify(this.announceInfo)
            }).then(response => {
                return response.json()
            }).then(json => {
                if (json.ret === -1) {
                    reject(json.data.msg);
                } else {
                    const data = json.data;
                    this.peerId = data.id;                            // peerId
                    this.btStats(data.report_interval);                    // 
                    // getPeersURLstatsURL
                    this.getPeersURL = `${this.baseUrl}/channel/${this.channelId}/node/${this.peerId}/peers`;
                    this.statsURL = `${this.baseUrl}/channel/${this.channelId}/node/${this.peerId}/stats`;

                    resolve(json.data);
                }
            }).catch(err => {
                logger.error(`btAnnounce error ${err}`);
                reject(err)
            })
        })
    }

    // deprecated
    btHeartbeat(interval = 10) {
        // const { logger } = this.engine;
        // this.heartbeater = window.setInterval(() => {
        //     fetch(this.heartbeatURL+`&peer_id=${this.peerId}`, {
        //         headers: {
        //             "X-Key": this.key,
        //         }
        //     }).then(response => {
        //
        //     }).catch(err => {
        //         window.clearInterval(this.heartbeater);
        //         logger.error(`btHeartbeat error ${err}`);
        //     })
        // }, interval * 1000)

    }

    btStats(interval = 10) {
        const { logger } = this.engine;
        this.heartbeater = window.setInterval(() => {
            fetch(this.statsURL, {
                headers: this._requestHeader,
                method: 'PUT',
                body: JSON.stringify(this._makeStatsBody())
            }).then(response => {
                if (response.ok) {
                    logger.info(`sucessfully report stats`);
                    this.httpDownloaded = 0;
                    this.p2pDownloaded = 0;
                    this.conns = 0;
                    this.failConns = 0;
                    this.errsFragLoad = 0;
                    this.errsBufStalled = 0;
                    this.errsInternalExpt = 0;
                }
            }).catch(err => {
                window.clearInterval(this.heartbeater);
                logger.error(`btStats error ${err}`);
            })
        }, interval * 1000)
    }

    btGetPeers() {
        const { logger } = this.engine;
        return new Promise((resolve, reject) => {
            fetch(this.getPeersURL, {
                headers: this._requestHeader,
                method: 'POST',
                // body: JSON.stringify({})
            }).then(response => {
                return response.json()
            }).then(json => {
                if (json.ret === -1) {
                    reject(json.data.msg);
                } else {
                    resolve(json.data);
                }
            }).catch(err => {
                logger.error(`btGetPeers error ${err}`);
                reject(err)
            })
        })
    }

    increConns() {                                              //()
        this.conns ++;
    }

    decreConns() {
        this.conns --;
    }

    increFailConns() {
        this.failConns ++;
    }

    reportFlow(stats, p2p = true) {
        const flow =  Math.round(stats.total/1024)
        if (p2p) {
            this.p2pDownloaded += flow;
            this.totalP2PDownloaded += flow;
        } else {
            this.httpDownloaded += flow;
            this.totalHTTPDownloaded += flow;
        }
        this._emitStats();
        // this._checkFlowLimit();
        // log(`cdnDownloaded ${this.cdnDownloaded} p2pDownloaded ${this.p2pDownloaded}`)
    }

    // 
    reportUploaded(size = 0) {
        this.totalP2PUploaded += Math.round(size/1024);
        this._emitStats();
    }

    destroy() {
        window.clearInterval(this.heartbeater);
    }

    _emitStats() {
        this.engine.emit('stats', {
            totalHTTPDownloaded: this.totalHTTPDownloaded,
            totalP2PDownloaded: this.totalP2PDownloaded,
            totalP2PUploaded: this.totalP2PUploaded
        });
    }

    // _checkFlowLimit() {                            //
    //     const { logger } = this.engine;
    //     if (this.p2pDownloaded >= this.limit || this.httpDownloaded >= this.limit) {         //(KB)
    //         const body = {
    //             source:  Math.round(this.httpDownloaded),    //KB
    //             p2p:  Math.round(this.p2pDownloaded)
    //         };
    //         if (this.conns !== 0) {
    //             body.conns = this.conns;
    //         }
    //         if (this.failConns > 0) {
    //             body.failConns = this.failConns;
    //         }
    //         if (this.errsFragLoad > 0) {
    //             body.errsFragLoad = this.errsFragLoad;
    //         }
    //         if (this.errsBufStalled > 0) {
    //             body.errsBufStalled = this.errsBufStalled;
    //         }
    //         if (this.errsInternalExpt > 0) {
    //             body.errsInternalExpt = this.errsInternalExpt;
    //         }
    //         logger.info(`reporting traffic p2p ${this.p2pDownloaded} http ${this.httpDownloaded}`);
    //         fetch(this.statsURL+`&peer_id=${this.peerId}`, {
    //             headers: {
    //                 "X-Key": this.key,
    //             },
    //             method: 'POST',
    //             body: JSON.stringify(body)
    //         }).then(response => {
    //             if (response.ok) {
    //                 logger.info(`sucessfully report traffic`);
    //                 this.httpDownloaded = 0;
    //                 this.p2pDownloaded = 0;
    //                 this.conns = 0;
    //                 this.failConns = 0;
    //                 this.errsFragLoad = 0;
    //                 this.errsBufStalled = 0;
    //                 this.errsInternalExpt = 0;
    //             }
    //         }).catch(err  => {
    //             logger.error(`stats upload error ${err}`);
    //         })
    //     }
    // }

    _makeStatsBody() {
        return {
            conns: this.conns,
            failConns: this.failConns,
            errsFragLoad: this.errsFragLoad,
            errsBufStalled: this.errsBufStalled,
            errsInternalExpt: this.errsInternalExpt,
            http:  Math.round(this.httpDownloaded),    //KB
            p2p:  Math.round(this.p2pDownloaded)
        }
    }

    get _requestHeader() {
        const headerInfo = {
            "X-Key": this.key,
            timestamp: new Date().getTime()
        };
        return headerInfo;
    }
}

export default Fetcher;


